import React from 'react';
import {
  Route, Redirect, Switch,
} from 'react-router';
import { Box } from 'ux-thebackyard';
import PrivateRoute from 'components/privateRoute';

import Login, { PATH as pathLogin } from 'scenes/login';
import Home, { PATH as pathHome } from 'scenes/home';

const App = () => (
  <Box width="100vw">
    <Switch>
      <Route path={pathLogin} component={Login} />
      <PrivateRoute path={pathHome} component={Home} />
      <Route path="*" render={() => <Redirect to={pathLogin} />} />
    </Switch>
  </Box>
);

export default App;
