import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { layout } from 'styled-system';
import useSelectors from 'helpers/useSelectors';
import { Redirect } from 'react-router';
import SrcBackground from 'media/01.jpg';
import { selectors as authSelectors, actions as authActions } from 'store/auth';

import { PATH as pathHome } from 'scenes/home';

import {
  Button,
  LogoPayroll,
  Text,
  Box,
  Spinner,
} from 'ux-thebackyard';

import Input from './Input';

const Form = styled('form')`
  width: 100%;
`;

const BackGroundImage = styled(Box)`
    background-image: url(${SrcBackground}); 
    background-size: cover;
    height: 100%;
    width:100%;
    ${layout}
`;

const WelcomeContainer = styled(Box)`
    background: linear-gradient(135deg,rgba(46,52,81,.2) 0%,rgba(46,52,81,.95) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    width: 100%;
    height: 100%;
    padding: 0 2.5rem;
`;

const WelcomeTitle = styled(Text)`
    line-height: 1.1;
`;

const FormContainer = styled(Box)`
    flex-direction: column;
    height: 100%;
    padding: 0 3rem;
    background-color: white;
    ${layout}
`;

const LoginLabel = styled(Box)`
  margin-bottom: 30px;
  width:100%;
`;

export const defaultUser = {
  email: '',
  password: '',
};

const failedStyles = {
  borderColor: 'red',
};

export default () => {
  const [user, setUser] = useState(defaultUser);
  const dispatch = useDispatch();

  const [
    authData,
    isBusy,
    isFailed,
  ] = useSelectors(
    authSelectors.signIn.getData,
    authSelectors.signIn.getIsBusy,
    authSelectors.signIn.getIsFailed,
  );

  const handleSignIn = () => dispatch(authActions.signIn.Call(user));
  const handleOnChange = ({ target: { value, name } }) => {
    setUser({ ...user, [name]: value });
    dispatch(authActions.signIn.NoStarted());
  };

  useEffect(() => {
    if (isFailed) setUser({ ...user, email: '' });
  }, [isFailed]);

  if (authData) return <Redirect to={pathHome} />;

  return (
    <Box display="flex" height="100vh">
      {isBusy && (
        <Box position="absolute" width="100%" height="100%" backgroundColor="transparent" centeredContent>
          <Spinner color="white" />
        </Box>
      )}
      <BackGroundImage display={['none', 'none', 'block']}>
        <WelcomeContainer>
          <WelcomeTitle fontSize="xxl" fontWeight="bld">Bienvenidos</WelcomeTitle>
        </WelcomeContainer>
      </BackGroundImage>
      <FormContainer centeredContent width={['100%', '100%', '35rem']} flexShrink="0">
        <LogoPayroll type="simple" alt="loginLogo" width="100px" mb="40px" />
        <LoginLabel fontSize="md">Iniciar Sesión</LoginLabel>

        <Form onSubmit={(e) => e.preventDefault()}>
          <Input
            id="email"
            // type="email"
            placeholder="Correo"
            name="email"
            value={user.email}
            onChange={handleOnChange}
            {...(isFailed ? failedStyles : {})}
          />
          <Input
            id="password"
            type="password"
            placeholder="Contraseña"
            // readOnly // TODO Delete after
            name="password"
            value={user.password}
            onChange={handleOnChange}
            {...(isFailed ? failedStyles : {})}
          />
          <Box centeredContent>
            <Button
              design="primary"
              rounded
              p="0.75rem 2.5rem"
              mt="2rem"
              id="button_submit"
              type="submit"
              // disabled={!user.password || !user.email}
              onClick={handleSignIn}
              label="Iniciar Sessión"
            />
          </Box>
        </Form>
      </FormContainer>
    </Box>
  );
};
