import { combineReducers } from 'redux';
import { call } from 'redux-saga/effects';
import RS from 'helpers/RS';

const NAME = 'document';

function* upload(api) {
  const data = yield call(api.uploadDocument);
  return data;
}

const uploadRS = RS({
  nestedPath: [NAME],
  storeName: 'upload',
  tryGenerator: upload,
});

/// ////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  uploadRS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...uploadRS.reducer,
  }),
};

export const actions = {
  upload: uploadRS.actions,
};

export const selectors = {
  upload: uploadRS.selectors,
};
