import React, { useState, useRef, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import FileInput from 'components/file-input';
import useSelectors from 'helpers/useSelectors';

import { actions, selectors } from 'store/document';

import {
  Box, Button, Spinner, Icon,
} from 'ux-thebackyard';
import { SvgCheckCircle } from 'react-line-awesome-svg';

const Home = () => {
  const [files, setFiles] = useState();
  const [showCheck, setShowCheck] = useState(false);
  const ref = useRef();
  const dispatch = useDispatch();
  const [
    isBusy,
    isSuccess,
  ] = useSelectors(
    selectors.upload.getIsBusy,
    selectors.upload.getIsSuccess,
  );
  const handleUpload = () => dispatch(actions.upload.Call());

  const handleSelectFiles = (e) => {
    setFiles([...e.target.files]);
    ref.current.value = '';
  };

  useEffect(() => {
    if (isSuccess) {
      setFiles(null);
      setShowCheck(true);
      setTimeout(() => setShowCheck(false), 5000);
    }
  }, [isSuccess]);

  return (
    <>
      <Box m="1rem" fontSize="xl">Documento Soporte</Box>
      <Box display="flex" alignItems="center" width="100vw">

        <Box width="60%">
          <FileInput
            accept=".xls, .xlsx"
            onChange={handleSelectFiles}
            ref={ref}
          />
        </Box>
        <Box ml="1rem">{files?.[0].name || 'No archivos seleccionados'}</Box>
        <Button
          label="Subir"
          design="primary"
          m="0 2rem 0 auto"
          onClick={handleUpload}
          disabled={!files}
        />
      </Box>
      <Box centeredContent height="30rem">
        {isBusy && <Spinner />}
        {showCheck && (
          <Icon
            svg={SvgCheckCircle}
            color="green"
            size="5rem"
          />
        )}

      </Box>
    </>
  );
};

export default Home;
